"use client"

import { BalanceCard } from "./balance-card"
import PaymentMethods from "./payment-methods"
import { TransactionHistory } from "./transaction-history"
import { useLanguage } from "@/contexts/language-context"
import { useEffect } from "react"

export function BillingContent() {
  const { t } = useLanguage()
  // –ê–≤—Ç–æ–ø—É–ª–ª–∏–Ω–≥ –ø–æ—Å–ª–µ —Ä–µ–¥–∏—Ä–µ–∫—Ç–∞ —Å –ø—Ä–æ–≤–∞–π–¥–µ—Ä–∞: –µ—Å–ª–∏ –µ—Å—Ç—å ?tx=..., –∫—Ä–∞—Ç–∫–æ –æ–ø—Ä–∞—à–∏–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ
  useEffect(() => {
    if (typeof window === "undefined") return
    const url = new URL(window.location.href)
    const hasTx = url.searchParams.get("tx")
    if (!hasTx) return

    let active = true
    const until = Date.now() + 60_000 // 60s –º–∞–∫—Å–∏–º—É–º

    const poll = async () => {
      try {
        // –æ–±–Ω–æ–≤–∏–º –ø—Ä–æ—Ñ–∏–ª—å –∏ –∏—Å—Ç–æ—Ä–∏—é (–¥–ª—è UI –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –∏—Ö —á–∏—Ç–∞—é—Ç)
        await Promise.all([
          fetch("/api/profile", { cache: "no-store" }),
          fetch("/api/transactions", { cache: "no-store" }),
        ])
      } catch {}
      if (!active) return
      if (Date.now() < until) {
        setTimeout(poll, 3000)
      } else {
        // –æ—á–∏—Å—Ç–∏–º –ø–∞—Ä–∞–º–µ—Ç—Ä –∏–∑ URL
        url.searchParams.delete("tx")
        window.history.replaceState({}, "", url.toString())
      }
    }
    poll()
    return () => { active = false }
  }, [])

  return (
    <div className="max-w-7xl mx-auto space-y-8">
      <div>
        <h1 className="text-3xl font-bold text-foreground mb-2">{t("dashboard.billing")}</h1>
        <p className="text-muted-foreground">
          {t("dashboard.billing") === "Billing"
            ? "Manage your balance and payments"
            : "–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –±–∞–ª–∞–Ω—Å–æ–º –∏ –ø–ª–∞—Ç–µ–∂–∞–º–∏"}
        </p>
      </div>

      <BalanceCard />
      <PaymentMethods />
      <TransactionHistory />
    </div>
  )
}
